<template>
    <div class="blog">
        <div class="header">
            <div :class="{ active: isoneword }" @click="topath('word')">
                随机一言
            </div>
            <div :class="{ active: iscat }" @click="topath('cat')">猫</div>
            <div :class="{ active: israinbow }" @click="topath('rainbow')">
                彩虹
            </div>
            <div :class="{ active: isengword }" @click="topath('english')">
                每日英语
            </div>
        </div>

        <div class="content">
            <div v-show="isoneword">{{ data.oneword }}</div>
            <div v-show="iscat">
                <img :src="data.catsrc" class="catimg" alt="" />
            </div>
            <div v-show="israinbow">{{ data.rainbow }}</div>
            <div v-show="isengword">
                <div>{{ data.eng0 }}</div>
                <button @click="ischinese = true">翻译</button>
                &nbsp;&nbsp;
                <button @click="topath('english')">下一条</button>
                <div v-show="ischinese">{{ data.eng1 }}</div>
            </div>
        </div>

        <div class="new" style="margin: 50px 0">
            <input type="text" v-model="mymusic" />
            <button @click="newmusic">music</button>
            <br />
            <button @click="newacg">acg</button>
            <br />
            <button @click="newtianqi">天气</button>
            <br />
            <button @click="newyu">鱼</button>
        </div>
    </div>
</template>
<script>
export default {
    name: "blog",
    data() {
        return {
            isoneword: true,
            iscat: false,
            israinbow: false,
            isengword: false,
            data: {
                catsrc: "",
                oneword: "",
                rainbow: "",
                eng0: "",
                eng1: "",
            },
            ischinese: false,
            mymusic: "",
        };
    },
    components: {},
    methods: {
        topath(path) {
            switch (path) {
                case "word":
                    // 松鼠-随机一言
                    this.$axios({
                        method: "get",
                        url: "https://api.szfx.top/yiyan/?code=json",
                    }).then((res) => {
                        this.data.oneword = res.data.msg;
                    });
                    this.isoneword = true;
                    this.iscat = this.isengword = this.israinbow = false;
                    break;
                case "cat":
                    this.$axios({
                        method: "get",
                        url: "https://api.thecatapi.com/v1/images/search?limit=1",
                    }).then((res) => {
                        this.data.catsrc = res.data[0].url;
                    });
                    this.iscat = true;
                    this.isoneword = this.isengword = this.israinbow = false;
                    break;
                case "rainbow":
                    // 大米API - 彩虹屁
                    this.$axios({
                        method: "get",
                        url: "https://api.qqsuu.cn/api/dm-caihongpi",
                    }).then((res) => {
                        this.data.rainbow = res.data.data.content;
                    });
                    this.israinbow = true;
                    this.iscat = this.isengword = this.isoneword = false;
                    break;
                case "english":
                    // 大米API - 每日英语
                    this.$axios({
                        method: "get",
                        url: "https://api.qqsuu.cn/api/dm-everyday",
                    }).then((res) => {
                        this.ischinese = false;
                        this.data.eng0 = res.data.data.content;
                        this.data.eng1 = res.data.data.note;
                    });
                    this.isengword = true;
                    this.iscat = this.isoneword = this.israinbow = false;
                    break;
            }
        },
        newmusic() {
            // https://api.szfx.top/music163/?song=最美的期待&order=0
            if (this.mymusic == "") {
                console.log("请输入信息");
            } else {
                this.$axios({
                    method: "get",
                    url:
                        "https://api.szfx.top/music163/?song=" +
                        this.mymusic +
                        "&order=0",
                }).then((res) => {
                    console.log(res);
                    console.log(res.data);
                });
            }
        },
        newacg() {
            this.$axios({
                method: "get",
                url: "https://api.szfx.top/acg//upyun",
            }).then((res) => {
                console.log(res);
                console.log(res.data);
            });
        },
        newtianqi() {
            // https://api.szfx.top/tianqi/
            this.$axios({
                method: "get",
                url: "https://api.szfx.top/tianqi/",
            }).then((res) => {
                console.log(res);
                console.log(res.data);
            });
        },
        newyu() {
            // https://api.szfx.top/tianqi/
            this.$axios({
                method: "get",
                url: "https://api.qqsuu.cn/api/dm-moyu",
            }).then((res) => {
                console.log(res);
                console.log(res.data);
            });
        },
    },
    mounted() {
        this.topath("word");
    },
};
</script>
<style lang="less" scoped>
.blog {
    width: 100%;
    height: 100%;

    .header {
        display: flex;
        max-width: 300px;
        justify-content: space-around;
        margin: 0 auto;
        div {
            cursor: pointer;
            white-space: nowrap;
        }
        div:hover {
            color: orange;
        }
        .active {
            color: orange;
        }
    }
    .content {
        .catimg {
            height: 150px;
            display: block;
        }
    }
}
</style>